// ============================================================================
// MOBILE FIRST
.landing-page {
  text-align: center;
  
  padding: 5vh 8%;
  background:
    #F8D6DD
    radial-gradient(#F8D6DD 0%, #F8D6DD 50%, #D994A2 90%);
  min-height: 100vh;
  color: white;

  .zombie-hand {
    z-index: 20;
    position: absolute;
    top: 20vh;
    right: 53vw;
    width: 80vw;
  }
}

.landing-page-a {
  display: block;
  max-width: 100%;
  max-height: 60vh;
  position: relative;

  > img {
    position: relative;
    max-height: 40vh;
    max-width: 48vw;
    width: auto;
    height: auto;
  }
}

.landing-page-b {
  display: block;
  width: 100%;
  padding: 0px;

  h1 {
    display: inline-block;
    color: #9056B5;
    font-weight: bold;
    font-size: 36px;
    line-height: 24px;
    text-shadow:
      0px 0px 10px #FFF,
      0px 0px 10px #FFF;
    margin: 0px;
    padding: 3vh 0px 2vh;
  }

  p {
    color: #000;
    line-height: 25px;
    margin: 0;
    padding: 1vh 0px 2vh;
  }

}

.mailchimp {
  > div {
    > div {
      margin-bottom: 8px;;
    }
  }
}

.social-icons {
  position: fixed;
  top: 16px;
  right: 12px;
  padding: 0;

  > a:last-child {
    margin-left: 8px;
  }
}

// ============================================================================
// Larger Phones (taller)
@media screen and (min-height: 700px) {

  .landing-page-a {
    > img {
      max-height: 40vh;
    }
  }

  .landing-page-b {
    h1 {
      font-size: 40px;
      line-height: 30px;
    }
  }

}


// ============================================================================
// Desktop
@media screen and (min-width: 960px) {

  .landing-page {
    padding: 12vh 16vw;

    .zombie-hand {
      top: 16vh;
      right: 65vw;
      width: 700px;
    }
  }
  
  .landing-page-a {
    float: left;
    width: 50%;
    max-height: initial;
    text-align: right;
  
    > img {
      max-height: 75vh;
      max-width: initial;
      width: auto;
      height: auto;
    }
  }
  
  .landing-page-b {
    float: right;
    width: 45%;

    text-align: left;
    padding-top: 20vh;
    
    h1 {
      display: block;
      font-size: 80px;
      line-height: 64px;
      text-shadow:
        0px 0px 10px #FFF,
        0px 0px 10px #FFF;
      margin: 5px 0px;
      padding: 5px 0px;
    }
  
    p {
      font-size: 24px;
      line-height: 40px;
    }

    input {
      width: 300px;
    }

    .button,
    button {
      width: 300px;
      margin: 8px 0;
    }  
  }
  
  .social-icons {
    position: relative;
    top: auto;
    right: auto;
    padding: 12px 0 0;

    > a {
      > img {
        width: 32px;
      }

      &:last-child {
        margin-left: 16px;
      }
    }
  }

}
